import React, { useState } from 'react';
import { ethers } from 'ethers';
import DFundABI from '../truffle_abis/DFund.json';
import { CONTRACT_ADDRESS } from '../web3/DFundContract';
import { ProjectStatus, isFundableStatus, getStatusLabel } from '../utils/statusUtils';  //     

function CheckProject() {
  const [projectId, setProjectId] = useState('');
  const [project, setProject] = useState(null);
  const [status, setStatus] = useState('');

  const handleCheck = async () => {
    if (!window.ethereum) {
      alert('Metamask not detected');
      return;
    }

    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const contract = new ethers.Contract(CONTRACT_ADDRESS, DFundABI.abi, provider);
      const data = await contract.projects(projectId); // public mapping getter
      const balance = await contract.getTotalDonated(projectId);
  
      if (data.id.toNumber() == 0 || data.title == "") {
        setStatus('  ID   .');
        setProject(null);
        return;
      }
      
      setProject({
        id: data.id.toString(),
        creator: data.creator,
        title: data.title,
        description: data.description,
        goalAmount: ethers.utils.formatEther(data.goalAmount),
        deadline: data.deadline.toNumber(),
        expertReviewRequested: data.expertReviewRequested,
        fundedAmount: ethers.utils.formatEther(balance),
        status: data.status
      });
      setStatus(' Project found');
    } catch (error) {
      console.error(error);
      setStatus(' Project not found or error occurred');
      setProject(null);
    }
  };

  return (
    <div style={{ maxWidth: '600px', margin: '2rem auto', fontFamily: 'sans-serif' }}>
      <h2>  </h2>
      <input
        type="number"
        placeholder=" ID "
        value={projectId}
        onChange={(e) => setProjectId(e.target.value)}
        style={{ width: '100%', marginBottom: '1rem' }}
      />
      <button onClick={handleCheck}></button>
      <p>{status}</p>

      {project && (
        <div style={{ marginTop: '1rem' }}>
          <p><strong> :</strong> {project.title}</p>
          <p><strong> :</strong> {project.description}</p>
          <p><strong>  :</strong> {project.goalAmount} ETH</p>
          <p><strong>  :</strong> {project.fundedAmount} ETH</p>
          <p><strong> :</strong> {new Date(project.deadline * 1000).toLocaleString()}</p>
          <p><strong>   :</strong> {project.expertReviewRequested ? '' : ''}</p>
          <p><strong>  :</strong> {project.creator}</p>
          <p><strong>  :</strong> {getStatusLabel(project.status)}</p>
        </div>
      )}
    </div>
  );
}

export default CheckProject;